@font-face {
  font-family: "Poppins-Regular";
  src: url("../fonts/Poppins-Regular.eot");
  src: url("../fonts/Poppins-Regular.eot?#iefix") format("embedded-opentype"),
    url("../fonts/Poppins-Regular.woff2") format("woff2"),
    url("../fonts/Poppins-Regular.woff") format("woff"),
    url("../fonts/Poppins-Regular.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

:root {
  --gray: #bdbdbd;
  --gray-light: #e0e0e0;
  --purple: #aa66cc;
  --purple-dark: #9933cc;
  --purple-border: #b993ce;
  --purple-dark2: #6a1b9a;
  --purple-light: #f3e5f5;
  --purple-shadow: #774c8f;
}

body {
  font-family: "Poppins-Light", Verdana, Geneva, Tahoma, sans-serif !important;
  margin: 0;
  overflow-x: hidden;
  height: 100%;
  font-size: 1rem;
  background: url("../img/back.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
}

.main {
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.card {
  background: white;
  box-shadow: 0px 1px 13px var(--purple-shadow);
  width: 500px;
  border-radius: 10px;
  border: 1px solid var(--purple-border);
}

.card-header {
  background-color: var(--purple-dark);
  padding: 0.75rem 1.25rem;
  border-bottom: 1px solid var(--purple-dark2);
  box-shadow: 0px 2px 4px var(--purple-dark2);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  color: white;
}

.card-body {
  flex: 1 1 auto;
  min-height: 1px;
  padding: 1.25rem;
  display: flex;
  flex-direction: row;
}

.login-form {
  align-self: center;
  width: 100%;
  height: auto;
}
.img-wrapper {
  align-self: center;
  margin-right: 1rem;
  border-radius: 50% !important;
  box-sizing: border-box;
  box-shadow: 0px 1px 5px var(--gray);
  padding: 0.5rem !important;
}

.form-image {
  margin: 1.2rem;
  width: 86px;
  height: auto;
}
.form-wrapper {
  display: flex;
  flex-direction: column;
  width: 100%;
}
.form-input {
  display: block;
  height: auto;
  padding: 0.5rem 0.75rem;
  font-size: 1rem;
  line-height: 1.5;
  border: 1px solid var(--gray);
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out box-shadow 0.15s ease-in-out;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
  width: 100%;
}

.form-input:focus {
  outline: none;
  border-color: var(--purple-dark);
  box-shadow: 0px 1px 4px var(--purple);
}

.form-button {
  outline: none;
  background: var(--purple);
  border: 0px solid var(--purple);
  color: white;
  font-weight: 400;
  border-radius: 0.25rem;
  user-select: none;
  line-height: 1.5rem;
  font-size: 1rem;
  width: 100%;
  transition: all 0.15s ease-in-out;
  padding: 0.35rem;
  margin-top: 12px;
  margin-bottom: 12px;
}

.home-button {
  margin-top: 1px;
  margin-bottom: 1px;
}

.form-button:hover {
  cursor: pointer;
  box-shadow: 0px 1px 6px var(--purple-dark2);
}

.invalid-field {
  font-size: 0.8rem;
  color: rgb(255, 0, 119);
  font-weight: bold;
  margin-bottom: 7px;
}
.link-wrapper {
  margin-top: 10px !important;
}
.link {
  color: var(--purple);
  font-size: 1rem;
  font-weight: bold;
  text-decoration: none;
  transition: all 0.2s ease-in-out;
}

.link:hover {
  color: var(--DatiOrange);
}

@media screen and (max-width: 550px) {
  .card {
    width: 80%;
    min-width: 150px;
  }

  .img-wrapper {
    display: none;
  }

  .card-header {
    font-size: 0.9rem;
    height: 30px;
    padding: 0.1rem 1.25rem;
  }

  .form-input {
    font-size: 0.8rem;
  }

  .link {
    font-size: 0.8rem;
  }
}
