.topbar {
  margin: 0;
  padding: 0;
  width: 200px;
  background-color: #9933cc;
  width: 100%;
  height: 70px;
  overflow: auto;
  color: white;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.topbar-brand-wrapper {
  display: flex;
  flex-direction: column;
  position: fixed;
  justify-content: center;
  right: 0px;
  margin: 1px 20px;
}
.topbar-brand {
  font-size: 1.5rem;
  font-weight: bold;
  color: #ffff8d;
  padding: 0px;
  margin: 0px;
  text-shadow: 0px 1px 17px white;
}

.topbar-brand-action {
  justify-self: end;
  font-size: 0.8rem;
  color: white;
  padding: 0px;
  margin: 5px;
  text-align: left;
  text-decoration: none;
  transition: color 0.3s ease-in-out;
}

.topbar-brand-action:hover {
  color: orange;
}

.form-inline {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  margin-left: 10px;
}
.form-inline label {
  margin: 5px 10px 5px 0;
}

.form-inline input {
  vertical-align: middle;
  margin: 5px 10px 5px 0;
  padding: 7px;
  background-color: #fff;
  font-size: 1rem;
  border: 1px solid var(--gray);
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out box-shadow 0.15s ease-in-out;
  margin-bottom: 0.5rem;
  box-sizing: border-box;
}

.form-inline input:focus {
  outline: none;
  border-color: var(--gray);
  box-shadow: 0px 1px 4px white;
}

.form-inline button {
  outline: none;
  background: white;
  border: 0px solid var(--purple-light);
  color: var(--purple-dark2);
  font-weight: 400;
  border-radius: 0.25rem;
  user-select: none;
  line-height: 1.5rem;
  font-size: 1rem;
  font-weight: bold;
  transition: all 0.15s ease-in-out;
  padding: 0.35rem 1rem;
  margin-top: 12px;
  margin-bottom: 12px;
}

.form-inline button:hover {
  cursor: pointer;
  box-shadow: 0px 1px 6px white;
}

@media (max-width: 650px) {
  .topbar {
    height: auto;
    min-width: 290px;
  }
  .form-inline input {
    margin: 10px 0;
  }

  .form-inline {
    flex-direction: column;
    align-items: stretch;
  }

  .topbar-brand-wrapper {
    top: 70px;
    margin-right: 0px;
  }
  .form-inline input {
    width: 40%;
  }
  .form-inline button {
    width: 40%;
  }

  .topbar-brand {
    display: none;
  }

  .topbar-brand-action {
    margin-top: 20px;
  }

  .form-inline button {
    font-weight: 200;
    line-height: 1.2rem;
    font-size: 0.8rem;
    margin-top: 1px;
  }

  .form-inline input {
    font-size: 0.8rem;
  }
}
